  background-repeat: no-repeat;
  background-size: contain;

  max-height: 100vh;
}

@media print {
  page[size="A4"] {
      background-color: #1a4567 !important;
      -webkit-print-color-adjust: exact; 
      margin: 0;
      box-shadow: 0;
  }
}
